* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: geometricPrecision;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none !important;
}

body, html,
div, p,
header, main, section, article,
nav, ul, ol, li,
h1, h2, h3, h4, h5, h6,
blockquote, form, figure {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, strong, b {
  font-weight: normal;
  font-size: inherit;
}

a,
a:focus {
  color: inherit;
  text-decoration: none;
  outline: none;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

strong, b {
  font-weight: 600;
}

ul, ol, li {
  list-style-type: none;
}

input,
textarea,
select,
button {
  margin: 0;
  padding: 0;
  border: 0 none;
  border-radius: 0;
  outline: 0;
  resize: none;
  background-color: initial;
  -webkit-appearance: none;
}

img {
  max-width: 100%;
}

.svg-icon-lib {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

button {
  background: transparent;
}

@font-face {
  font-family: "Times New Roman";
  src: url("${path-font}/GrandHotel-Regular.woff2") format("woff2"), url("${path-font}/GrandHotel-Regular.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Times New Roman";
  src: url("${path-font}/Montserrat-Bold.woff2") format("woff2"), url("${path-font}/Montserrat-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Times New Roman";
  src: url("${path-font}/Montserrat-Regular.woff2") format("woff2"), url("${path-font}/Montserrat-Regular.woff") format("woff");
  font-weight: bold;
  font-style: italic;
}
@font-face {
  font-family: "Times New Roman";
  src: url("${path-font}/Montserrat-Thin.woff2") format("woff2"), url("${path-font}/Montserrat-Thin.woff") format("woff");
  font-weight: normal;
  font-style: italic;
}
body {
  font-family: "Montserrat";
  background: #303030;
  margin: 0 auto;
  width: 1440px;
}

.container {
  max-width: 100%;
  padding: 0 10px;
}

.container-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

ul {
  padding: 0;
  margin: 0;
}
ul li {
  list-style: none;
  margin: 0;
}

.cta {
  width: 250px;
  max-width: 100%;
  color: #333333;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  background: #ffc200;
  border-radius: 5px;
  padding: 15px 0;
  cursor: pointer;
  transition: transform 0.3s ease-out;
}
.cta:hover {
  transform: scale(1.05);
}

header {
  width: 100%;
  display: block;
  margin: 40px 0 10px;
  padding: 0 50px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}
header .menu ul li {
  display: inline-block;
  margin: 0 35px;
}
header .menu ul li a {
  font-size: 25px;
  color: white;
  text-decoration: none;
  transition: font-weight 0.3s ease-out;
}
header .menu ul li a:hover {
  font-weight: bold;
}
header .search-icon {
  width: 320px;
  height: 32px;
  position: relative;
  border-bottom: 1px solid black;
}
header .search-icon:before {
  content: "";
  width: 32px;
  height: 32px;
  background: url("../img/lupa.png") no-repeat center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.hero {
  position: relative;
  height: 796px;
}
.hero .hero-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.hero .pencil-bg {
  width: 1010px;
  height: 100%;
  background: url("../img/pencil-bg.png") no-repeat center;
  background-size: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero .pencil-bg .text-box {
  width: 620px;
  max-width: 100%;
}
.hero .pencil-bg .text-box .main-title {
  font-family: "Grand Hotel";
  font-size: 80px;
  color: #ffc200;
  margin-bottom: 20px;
}
.hero .pencil-bg .text-box .description {
  font-size: 30px;
  color: white;
  margin-bottom: 30px;
}